





























import { defineComponent, PropType } from '@vue/composition-api';
import { Todo } from '@/types';

export default defineComponent({
  props: {
    readonly: Boolean,
    edit: Boolean,
    todo: {
      type: Object as PropType<Todo>,
      required: true,
    },
  },
  setup(props, ctx) {
    const onClickDelete = () => {
      ctx.emit('delete', props.todo.id);
    };

    const change = () => {
      ctx.emit('change');
    };

    return { onClickDelete, change };
  },
});
